.navbar{
   padding-top: 20px;
   padding-bottom: 10px;
   opacity: 0.7;

  
    
  .swiper{
    &-wrapper{
      .swiper-slide{
        position: relative;
        &::after{
          position: absolute;
          content: "";
          width: 100%;
          height: 2px;
          background: #000;
          margin-top: 2px;
          opacity: 0
        }
        &:hover::after{
         opacity: 1;
         transition: all 0.3s linear;
        }
      }
    }
  }
  .nav-wrapper{
    padding: 3px 0;
   
  }
    .navbar-item{
      
        text-align: center;
        color: $textSub;
        width: 100%;
        img{
            width: 25px;
            color: $textSub;
            
        }
        p{
            font-size: 10px;
        }
        &:active img{
          @include Active-button;
        }
       
    }

    .swiper-button-next {
        background-image: url(../../assest/image/arrow-right.png);
        background-repeat: no-repeat;
        background-size: 100% auto;
        background-position: center;
        background-color: #fff;
        transform: translateY(-25%);
        width: 20px;
        right: 0;
        display: none;
        @include Responsive(MD){
          display: block;
         }

      }
      
      .swiper-button-next::after {
        content: '';
      }

      .swiper-button-prev {
        background-image: url(../../assest/image/back.png);
        background-repeat: no-repeat;
        background-size: 100% auto;
        background-position: center;
        background-color: #fff;
        transform: translateY(-25%);
       width: 20px;
        left: 0;
        display: none;
        @include Responsive(MD){
          display: block;
         }

      }
      
      .swiper-button-prev::after {
        content: '';
        
      }
}